export default "/**\n  * Copyright 2020, Verizon Media\n  * Licensed under the terms of the MIT license. See LICENSE file in project root for terms.\n  */\n\nimport React from 'react';\nimport Button from '@material-ui/core/Button';\nimport Field from '../../../../components/Field';\nimport Form from '../../../../components/Form';\nimport Styled from '../../../components/StyledComponents';\nimport DataViewer from './custom-form-components/DataViewer';\nimport formEdit from './edit-form';\nimport formView from './view-form';\n\nexport default class App extends React.Component {\n  constructor(props) {\n    super(props);\n\n    this.formEdit = formEdit;\n    this.formView = formView;\n\n    this.state = {\n      form: this.formEdit,\n      edit: true,\n    };\n  }\n\n  render() {\n    return (\n      <React.Fragment>\n        <Form model={this.state.form.model} resources={this.state.form.resources}>\n          <Styled.MainElement>\n            <Field id=\"id\" />\n            <Field id=\"firstName\" />\n            <Field id=\"lastName\" />\n            <Button onClick={this.change}>Change Form</Button>\n          </Styled.MainElement>\n          <Styled.MainElement>\n            <DataViewer />\n          </Styled.MainElement>\n        </Form>\n      </React.Fragment>);\n  }\n\n  change = () => {\n    this.setState({\n      edit: !this.state.edit,\n      form: this.state.edit ? this.formView : this.formEdit,\n    });\n  }\n}\n";