export default "import React from 'react';\nimport Button from '@material-ui/core/Button';\nimport FormContext from '../../../../../components/context';\n\nexport default class SaveButton extends React.Component {\n  static contextType = FormContext;\n\n  render() {\n    return (\n      <Button aria-label=\"Save\" color=\"primary\" variant=\"contained\"\n        disabled={!this.context.model.dirty || this.context.model.invalid || this.context.model.processing}\n        onClick={this.save}>{this.props.children}</Button>\n    );\n  }\n\n  save = () => {\n    console.log('Saving data to the server...', this.context.model.data); // eslint-disable-line\n  }\n}\n";