export default "const service = {\n  fetchItems: query => new Promise((resolve) => {\n    // mock server call\n    setTimeout(() => {\n      const allServerItems = [{\n        value: 'BASKETBALL',\n        label: 'Basketball',\n      }, {\n        value: 'FOOTBALL',\n        label: 'Football',\n      }, {\n        value: 'SHOP',\n        label: 'Shop',\n      }, {\n        value: 'FASHION',\n        label: 'Fashion',\n      }, {\n        value: 'COOK',\n        label: 'Cook',\n      }];\n\n      const items = allServerItems.filter(x => x.label.toLowerCase().indexOf(query.toLowerCase()) > -1);\n\n      resolve(items);\n    }, 1);\n  }),\n};\n\nexport default service;\n";